}

header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 0.5rem;
  max-width: 100%;
}

svg {
  width: 1rem;
  height: 1rem;
}

.fullName {
  color: #377ea4;
}

.info {
  margin: 0.25rem;
  font-weight: 400;
  font-size: 0.8rem;
}

span {
  display: flex;
  align-items: center;
}

.position {
  font-size: 1.7rem;
}

button {
  color: white;
  background-color: #2d5cfb;
  border: none;
  width: 35%;
  border-radius: 20px;
  display: block;
  margin: 1.4rem;
  padding: 1rem 1.7rem;
  cursor: pointer;
}

button svg {
  position: relative;
  top: 3px;
  margin-right: 4px;
}

button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.skillList {
  width: 80%;
  margin: auto;
  font-size: 0.8rem;
  display: flex;
  flex-flow: wrap;
}

.skill {
  margin: 0.5rem 2rem 0.5rem 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: 'Lato', sans-serif;
}

main {
  display: flex;
  padding: 3%;
  background-color: #282828;
}

.cvInfo {
  width: 55%;
  min-height: 90vh;
  margin: 0 5%;
  padding: 3%;
  outline: 1px solid;
  background-color: white;
}

.addFields {
  color: white;
  flex-flow: column;
  display: flex;
  align-items: flex-start;
  padding-left: 4rem;
  justify-content: center;
  width: 45%;
}

